<template>
    <el-container style="height:100%;" direction="vertrcal">
        <!-- header -->

        <!-- main -->
        <el-main  class="el-main-3">
            <div class="header-2">
                <p class="p-2"> <i class="iconfont">&#xe61a;</i> 
                </p>
            </div>
            <hr/>
            <el-row>
                <!-- main-1 -1-->
                <div class="main-1">
                    <!--   main-1-1  -->
                    <div class="main-1-1">
                        <!--  -->
                        <div class="main-1-1-1">
                            <p class="main-p1"> </p>
                            <!-- ********************************** -->
                            <p class="main-p2" @click="moreAlarmInfoDisplay = true"></p>
                        </div>
                        <el-dialog title="" :visible.sync="moreAlarmInfoDisplay" style="width: 2000px;">
                            <el-table
                                    :header-cell-style="{background:'#E95513',padding:0,color:'#FFFFFF'}"
                                    height=274
                                    row-style="30px"
                                    cell-style="padding:0"
                                    class="table1"
                                    :data="moreAlarmInfoData.slice((currentPage-1)*pagesize,currentPage*pagesize)"
                                    style="width: 100%; ">
                                <el-table-column
                                        prop="index"
                                        label=""
                                        width="100"
                                        :index="indexMethod">
                                </el-table-column>
                                <el-table-column
                                        prop="warnType"
                                        label=""
                                        width="240">
                                </el-table-column>
                                <el-table-column
                                        prop="warn"
                                        label=""
                                        width="200">
                                </el-table-column>
                                <el-table-column
                                        prop="time"
                                        width="215"
                                        label="">
                                </el-table-column>
                            </el-table>
                            <div style="width: 100%;height:20px">
                                <div class="p-page" style="font-size: 12px;padding-left: 34px">{{(currentPage-1) * pagesize +1}}{{((currentPage * pagesize)<(moreAlarmInfoData.length))?currentPage * pagesize:moreAlarmInfoData.length}}{{moreAlarmInfoData.length}}
                                    <span style="position: relative;left: 33px;font-size: 12px;"></span>
                                    <el-select v-model="pagesize" slot="prepend" placeholder="" id="pagesize" style="width: 65px;height: 30px;border-radius: 0px;font-size: 12px;left: 35px;">
                                        <el-option label="10" value="10"></el-option>
                                        <el-option label="20" value="20"></el-option>
                                    </el-select>
                                    <span style="margin-left:2px;position: relative;left: 32px"><span style="margin-left: 20px"><el-input  v-model="jumper" style="width: 50px;height: 30px;margin-left: 2px;margin-right: 4px"></el-input></span><el-button class="button2" style="font-size: 12px;" @click="handleCurrentChange(jumper)"></el-button></span>
                                </div>

                                <div style="float:right;margin-top:10px;margin-right: 30px;">
                                    <!-- ********************************* -->
                                    <el-pagination
                                            background="#E95513"
                                            prev-text=""
                                            next-text=""
                                            jumper-text=""
                                            @size-change="handleSizeChange"
                                            @current-change="handleCurrentChange"
                                            :current-page="currentPage"
                                            :page-sizes="[10, 20]"
                                            :page-size="pagesize"
                                            :total="moreAlarmInfoData.length"
                                            layout="slot,prev, pager, next,total" >
                                        <!-- <slot name="as">dddd</slot> -->
                                    </el-pagination>
                                </div>
                            </div>
                        </el-dialog>
                        <!-- *********************** -->
                        <div class="main-1-1-2">
                            <el-table
                                    :header-cell-style="{background:'#E95513',padding:0,color:'#FFFFFF'}"
                                    height=274
                                    row-style="30px"
                                    cell-style="padding:0"
                                    class="table1"
                                    :data="newAlarmData"
                                    style="width: 100%; ">
                                <el-table-column
                                        type="index"
                                        label=""
                                        width="100"
                                        :index="indexMethod">
                                </el-table-column>
                                <el-table-column
                                        prop="warnType"
                                        label=""
                                        width="240">
                                </el-table-column>
                                <el-table-column
                                        prop="warn"
                                        label=""
                                        width="200">
                                </el-table-column>
                                <el-table-column
                                        prop="time"
                                        width="215"
                                        label="">
                                </el-table-column>
                            </el-table>
                        </div><!--main-1-1-2-->

                    </div> <!--main-1-1-->
                    <!-- main-1-2 -->
                    <div class="main-1-2">
                        <!--  -->
                        <div class="main-1-2-1">
                            <p class="main-p3"></p>

                        </div>
                        <!-- 1 -->
                        <div class="chatrs" >
                            <div id="myChart" style="width:100%; height:274px;max-width:1025px;position:relative;top: 35px;"></div>
                        </div>
                    </div> <!--main-1-2 -->
                </div><!--main-1-->
            </el-row>
            <el-row>
                <!-- main-1 -2-->
                <div class="main-2">
                    <div class=" main-2-1" >
                        <div class="main-2-1-1">
                            <p class="main-p4"></p>

                        </div>
                        <div class="chatrs-1" >
                            <div id="myChart2" style="width: 100%; height:290px;max-width:800px;"></div>
                        </div>
                    </div><!--main-2-1-->
                    <div class="main-2-2">
                        <div class="main-2-2-2">
                            <p class="main-p4"></p>
                        </div>
                        <div class="chatrs" >
                            <div id="myChart3" style="width: 100%; height:290px;max-width:1025px;"></div>
                        </div>
                    </div><!--main-2-1-->
                </div> <!--main-2-->
            </el-row>
        </el-main>
        <!-- footer -->

    </el-container>
</template>
<style scoped>

    .el-main-3{
        padding:2px ;
    }
    /* -1 */
    .main-1{
        height: 50%;
        width: 100%;
        /* font-family: ''; */
    }
    .main-1-1{
        width: 40%;
        height: 100%;
        float: left;
    }
    .main-1-1-1{
        width: 100%;
        height: 30px;
        background-color: #EDEDED;
        font-size: 11pt;
    }
    .main-1-1-2{
        height: 100%;
    }
    .main-1-2{
        width: 57%;
        height: 100%;
        float:right;
    }
    .main-1-2-1{
        width: 100%;
        height: 30px;
        background-color: #EDEDED;
        font-size: 11pt;

    }
    .main-2{
        height: 50%;
        width: 100%;
        /* font-family: ''; */
    }
    .main-2-1{
        width: 40%;
        height: 100%;
        float: left;
    }
    .main-2-1-1{
        margin-top:20px;
        width: 100%;
        height: 30px;
        background-color: #EDEDED;
        /* font-family: '';  */
        font-size: 11pt;
    }
    .main-2-2{
        width: 57%;
        height: 95%;
        float:right;
    }
    .main-2-2-2{
        margin-top:20px;
        width: 100%;
        height: 30px;
        background-color: #EDEDED;
        font-family: 'Microsoft YaHei';
        font-size: 11pt;
    }

    .main-p1{
        /*  */
        margin: 0;
        margin-top:4px;
        margin-left:10px;
        margin-bottom: 10px;
        font-family:'Microsoft YaHei';
        float: left;
    }
    .main-p2{
        margin: 0;
        margin-top:4px;
        margin-right:10px;
        float: right;
    }
    .main-p3{
        margin: 0;
        margin-top:4px;
        margin-left:10px;
        margin-bottom: 10px;
        font-family:'Microsoft YaHei';
        float: left;
    }
    .main-p4{
        margin: 0;
        margin-top:4px;
        margin-left:10px;
        margin-bottom: 10px;
        font-family:'Microsoft YaHei';
        float: left;
    }
    /****************************************** main  end */

</style>
<script>

    export default {


        data() {
            return {
                //
                moreAlarmInfoData:[],
                //
                currentPage: 1,
                jumper:1,
                pagesize:10,
                //
                moreAlarmInfoDisplay:false,
                //
                chart1_timeList2 :[0,0,0,0],
                chart1_sumList2 : [0,0,0,0],
                myChart2_data:[1,1,5000],
                administrator:'wulala',
                times:'2018-5-21',
                // ***************start
                newAlarmData: [{
                    date: '2016-05-03',
                    realtimewarning: '',
                    warningtype: ' ',
                    tag: ''
                },{
                    date: '2016-05-03',
                    realtimewarning: '',
                    warningtype: ' ',
                    tag: ''
                },{
                    date: '2016-05-02',
                    realtimewarning: '',
                    warningtype: '',
                    tag: ''
                },   {
                    date: '2016-05-02',
                    realtimewarning: '',
                    warningtype: '',
                },{
                    date: '2016-05-01',
                    realtimewarning: '',
                    warningtype: ' ',
                    tag: ''
                },{
                    date: '2016-05-01',
                    realtimewarning: '',
                    warningtype: ' ',
                    tag: ''
                },{
                    date: '2016-05-01',
                    realtimewarning: '',
                    warningtype: ' ',
                    tag: ''
                },{
                    date: '2016-05-01',
                    realtimewarning: '',
                    warningtype: ' ',
                    tag: ''
                }
                ],
                // *****************end

                // 
                chart3_timeList: [],
                chart3_sumList: [],

            }

        },
        mounted(){
            // *****************echarts  2
            this.getNewWarnMsg();
            this.getMoreWarnMsg();
            this.getWarningSum();
            this.getAllWarningSum();
            this.getWarnMsgNum();
            this.drawLine();
        },
        methods: {
            getMoreWarnMsg(){
                var that = this;
                this.$axios.get("/getMoreWarnMsg").then(function (response) {
                    that.moreAlarmInfoData = response.data;
                })
            },

            //
            getNewWarnMsg(){
                var that = this;
                this.$axios.get("/getNewWarnMsg").then(function (response) {
                    that.newAlarmData = response.data;
                })
            },

            getWarnMsgNum() {
                var that = this;
                this.$axios.get("/getWarnMsgNum").then(function (response) {
                    that.chart1_timeList2 = response.data.timeList;
                    that.chart1_sumList2 = response.data.sumList;
                    // alert(that.timeList2)
                    that.drawLine();
                })

                // // alert(this.timeList2)
                // alert(this.timeList2+"numlist")
            },
            getAllWarningSum(){
                var that = this;
                this.$axios.get("/getAllWarningSum").then(function (response) {
                    // alert(response.data);
                    that.chart3_timeList = response.data.timeList;
                    that.chart3_sumList = response.data.numList;
                    // alert(that.timeList);
                    that.drawLine();
                })

            },
            //
            getWarningSum(){
                var that = this;
                this.$axios.get("/getWarningSum").then(function (response) {
                    that.myChart2_data = response.data;
                    that.drawLine();

                });
            },
            handleSizeChange(size) {
                this.pagesize = size;
                console.log(` ${val} `);
            },
            handleCurrentChange(currentPage) {
                this.currentPage = currentPage;
                console.log(`: ${val}`);
            },
            drawLine(){
                // domecharts
                let myChart = this.$echarts.init(document.getElementById('myChart'))//
                let myChart2 = this.$echarts.init(document.getElementById('myChart2'))// 
                let myChart3 = this.$echarts.init(document.getElementById('myChart3'))// 
                // 
                //
                myChart3.setOption({
                        xAxis: {
                            type: 'category',
                            boundaryGap: false,
                            data: this.chart3_timeList
                        },
                        yAxis: {
                            type: 'value'
                        },
                    legend: {
                        data:['',''],
                        x: 'left'
                    },
                        series: [{
                            data: this.chart3_sumList,
                            type: 'line',
                            areaStyle: {
                                normal:{
                                    color:'rgba(112, 168, 255, 0.5)'
                                }
                            },
                            symbolSize: 10,
                            itemStyle: {
                                normal: {
                                    color: "#70A8FF",
                                    lineStyle: {
                                        color: "#70A8FF"
                                    }
                                }
                            },
                        }]
                    }

                );
                //
                myChart2.setOption({
                    title: {  },
                    tooltip: { },
                    // legend:{
                    //     data:['']
                    // },

                    xAxis: {
                        data: ["","",""]
                    },
                    yAxis: {},
                    series: [{
                        name: '',
                        type: 'bar',
                        data: this.myChart2_data,
                    }]
                })
                // 
                myChart.setOption({
                    xAxis: {
                        type: 'category',
                        boundaryGap: false,
                        data: this.chart1_timeList2
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: this.chart1_sumList2,
                        type: 'line',
                        areaStyle: {
                            normal:{
                                color:'rgba(112, 168, 255, 0.5)'
                            }
                        },
                        symbolSize: 10,
                        itemStyle: {
                            normal: {
                                color:"#70A8FF",
                                lineStyle: {
                                    color: "#70A8FF"
                                }
                            }
                        }
                    }]
                });
                // echart
                window.addEventListener("resize", function() {
                    //     myChart1.setOption(option1);
                    myChart.resize();
                    myChart2.resize();
                    myChart3.resize();

                });

            },

        }

    }


</script>



